@import "colors";

.grid__item {
  border: 3px solid #fff;
  border-radius: 8px;
  height: 370px;
  color: #fff;
  font-weight: 700;
  text-align: center;

  &:nth-child(1) {
    background-color: $blue-dark;
  }
  &:nth-child(2) {
    background-color: $orange-dark;
  }
  &:nth-child(3) {
    background-color: $blue-dark;
  }
  &:nth-child(4) {
    background-color: $blue-light;
  }
  &:nth-child(5) {
    background-color: $orange-light;
  }
  &:nth-child(6) {
    background-color: $blue-dark;
  }
  &:nth-child(7) {
    background-color: $orange-dark;
  }
  &:nth-child(8) {
    background-color: $blue-dark;
  }
  &:nth-child(9) {
    background-color: $blue-light;
  }
  &:nth-child(10) {
    background-color: $orange-light;
  }
  &:nth-child(11) {
    background-color: $blue-dark;
  }
  &:nth-child(12) {
    background-color: $orange-dark;
  }
}

.grid__text {
  padding: 30px 0;
  text-align: center;
}
